.nav { grid-area: nav; 
    padding: 14px 16px;
      font-family: Georgia, serif;
      position: absolute;
      display: flex;
      align-self: center;
      font-size: 25px; 
    }
    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
    }
    
    li {
      display: inline;
    }
    
    a {
      display: block;
      padding: 8px;
      background-color: #dddddd;
    }
    